.cartPage{
    width: 100%;
    margin: auto;
}

h1{
    margin-bottom: 30px;
}
ul{
    list-style: none;
}
li{ 
    display: flex;
    margin-bottom: 20px;
    width: 500px;
    margin: 0 auto;
    margin-bottom: 20px;
}

li img{
    width: 100px;
    margin-bottom: 10px;
    
}
.score-buble{
    display: inline-block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    font-size: 12px;
    background-color: red;
    color: #fff;
    text-align: center;
    position: absolute;
    left: 45px;
    bottom: 0px;
    padding-top: 3px;
}
.image-container{
    position: relative;
}
.title{
    font-size: 20px;
    flex: 1;
    max-width:400px;
    padding-top: 50px;
}

button{
    height: 30px;
    margin-top: 52px;
    padding: 0px 10px;
    width: 66px;
}