* {
  margin: 0;
  padding: 0;
  border: 0;
}


 .movie-card {
  flex: auto;
    flex-wrap: wrap;
    width: auto;
    height: auto;
    margin-bottom: 20px;
  }
  
  .card-title{
    color:rgb(83, 33, 33);
    font-size: 16px;

}
.card-text{
  font-size: 3px;
  color: black;
}
.allMovies{
  padding: 10px;
  background-color: black;
}

.homeTvshow{
  padding: 10px;
}

.gener-title{
  text-align: left;
  padding: 10px;
  font-size: 20px;
  padding-left: 15px;
}
#header-title{
  font-size: 40px;
}
  

.carussel-card {
  width:300px;
  position:relative;
  
}



.carussel-card button {
  position: absolute;
  bottom: 0px;
  left: 0px;
  color: red;
  background-color: bisque;
  border-radius: 20px
}
.carussel-card img{    height: 300px; border-radius: 14px;}
.carussel-card div {
  background-color: #6d6464b5;
  position: absolute;
  top: 10px;
  color: white;
  padding-left: 10px;
}